/**
 * @description测试redux
 */

let initState = "狗团"


function testReducers(state = initState, action) {
  let newState = JSON.parse(JSON.stringify(state))

  switch (action.type) {
    case "love":
      newState = "love"
      break
    case "hate":
      newState = "恨"
      break
    default:
      break
  }
  // sessionStorage.setItem('redux-user', JSON.stringify(newState))
  return newState
}

export default testReducers