import { menu } from "../router/index"
import { Layout, Menu } from 'antd'
import React, { useEffect, useState } from 'react'
import { useHistory, useLocation } from "react-router-dom"
import { get, set } from "../utils/storage"

const { Header, Sider, Content } = Layout


function MyLayout({ children }) {
    const [menuList, setMenuList] = useState([])
    const history = useHistory()
    const location = useLocation()

    const [selectedKeys, setSelectedKey] = useState([]) //当前选中的菜单项 key 数组
    const [openKeys, setOpenKeys] = useState([])
    //初始化生成菜单
    useEffect(() => {
        function loop(menu) {
            let _menuList = []
            menu.forEach(it => {
                let itemObj = {}
                const { children, ...res } = it
                itemObj.label = res.title
                itemObj.key = res.id
                itemObj.path = res.path
                itemObj.icon = res.icon
                if (!!children) itemObj.children = loop(children)
                _menuList.push(itemObj)
            })
            return _menuList
        }
        setMenuList(loop(menu))

    }, [])


    //页面刷新后,重新定位
    useEffect(() => {
        let _selectedKeys = get("setSelectedKey")
        let _openKeys = get("openKeys")
        try {
            let data = JSON.parse(_selectedKeys)
            let _openKeysData = JSON.parse(_openKeys)
            setSelectedKey(data)
            setOpenKeys(_openKeysData)
        } catch (error) {
            setSelectedKey([])
            setOpenKeys([])
        }
    }, [location.pathname])


    const btnEvt = ({ key, selectedKeys }) => {
        let path = ""
        for (const it of menu) {
            const { children, ...res } = it
            if (children) {
                let index = children.findIndex(oo => oo.id === key)
                if (index > -1) {
                    path = children[index].path
                    setSelectedKey(selectedKeys)
                    set("setSelectedKey", JSON.stringify(selectedKeys))
                    history.push({
                        pathname: path
                    })
                    return
                }
            }
        }

    }

    return (
        <div className='page-container'>
            <Layout>
                <Sider >
                    <header>
                        组件系统
                    </header>
                    <div className='menu-container'>
                        <Menu
                            items={menuList}
                            mode="inline"
                            selectedKeys={selectedKeys}
                            openKeys={openKeys}
                            onOpenChange={(val) => {
                                setOpenKeys(val)
                                set("openKeys", JSON.stringify(val))
                            }}
                            onSelect={({ key, selectedKeys, item, keyPath }) => {
                                btnEvt({ key, selectedKeys })
                            }}
                        />
                    </div>
                </Sider>
                <Layout>
                    <Header>组件系统</Header>
                    <Content>
                        <div className='content-container'>
                            {children}
                        </div>
                    </Content>
                </Layout>
            </Layout>
        </div>

    )
}

export default MyLayout

